import Vue from 'vue'
import Router from 'vue-router'
import Footer from "@/components/Footer";
import Drawer from "@/components/Drawer";
import Header from "@/components/Header";


Vue.use(Router);

function lazyLoad(view){
  return () => Promise.resolve(import(`@/views/${view}.vue`))
}

export default new Router({
  linkExactActiveClass: 'active',
  routes: [
    {
      path: '/',
      name: 'home',
      components: { default: lazyLoad('MainPage'), footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'white' }
      }
    },
    {
      path: '/about',
      name: 'about',
      components: { default: lazyLoad('About'), footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'white' }
      }
    },
    {
      path: '/contact',
      name: 'contact',
      components: { default: lazyLoad('Contact'), footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/partners',
      name: 'partners',
      components: { default: lazyLoad('Partners'), footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/popular',
      name: 'popular',
      components: { default: lazyLoad('Popular'), footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/latest',
      name: 'latest',
      components: { default: lazyLoad('Latest'), footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/search',
      name: 'search',
      components: { default: lazyLoad('Search'), footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/album',
      name: 'selected-album',
      components: { default: lazyLoad('SelectedAlbum'), footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'black' }
      }
    },
  ],
  scrollBehavior: to => {
    if (to.hash) {
      return { selector: to.hash };
    } else {
      return { x: 0, y: 0 };
    }
  }
});