import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    show_drawer: null,
    categories: [
      {text: 'Show all categories', href: 'www.google.com'},
      {text: 'Rock', href: 'www.google.com'},
      {text: 'Pop', href: 'www.google.com'},
      {text: 'Synth', href: 'www.google.com'},
      {text: 'Country', href: 'www.google.com'},
      {text: 'Metal', href: 'www.google.com'},
      {text: 'Opera', href: 'www.google.com'},
      {text: 'Reggae', href: 'www.google.com'},
      {text: 'Rap', href: 'www.google.com'},
      {text: 'Techno', href: 'www.google.com'},
      {text: 'Classical', href: 'www.google.com'},
      {text: '80\'s', href: 'www.google.com'},
      {text: '90\'s', href: 'www.google.com'},
      {text: 'Euro', href: 'www.google.com'},
      {text: 'Dance', href: 'www.google.com'},
    ],

    active_categories: ['Rock', 'Pop'],

    albums: [
      {type: 'Rock', title: 'Hard Rock', cover: 'https://images.homedepot-static.com/productImages/94af8836-0338-4802-914e-04cc71e562ad/svn/backyard-x-scapes-fake-rocks-hdd-rof-rocsb-64_1000.jpg'},
      {type: 'Rock', title: 'Strong Rock', cover: 'https://carolawetterholm.se/wp-content/uploads/2019/03/rock-workout-2.jpg'},
      {type: 'Pop', title: 'Old Pop', cover: 'https://i.ebayimg.com/images/g/K4cAAOSwv0tVCFS6/s-l300.jpg'},
      {type: 'Pop', title: 'Sweet Pop', cover: 'https://cdn.cdon.com/media-dynamic/images/product/toy/vidmatbordet/image740/intex_popsicle_badmadrass-43793210-45232351-org.jpg'},
      {type: 'Reggae', title: 'Reggae-fail-to-load', cover: 'not/a/real/url.png'},
      {type: 'Reggae', title: 'Reggae-big-image', cover: 'https://electricmonkonline.com/wp-content/uploads/2018/07/IMG_7020.jpg'},
      {type: 'Reggae', title: 'Reggae-big-image2', cover: 'https://electricmonkonline.com/wp-content/uploads/2018/07/IMG_7942.jpg'},
    ],

    drawer_pages: [
      {icon: 'mdi-fire', text: 'Most popular', route: 'popular'},
      {icon: 'mdi-clock', text: 'Latest releases', route: 'latest'},
      {icon: 'mdi-email', text: 'Contact us', route: 'contact'},
      {icon: 'mdi-information', text: 'About', route: 'about'},
      {icon: 'mdi-star', text: 'Partner sites', route: 'partners'},
    ],

    filtered_albums: [
    ],

    no_search_results: [
      {type: 'No results', title: 'No results found', cover: null},
    ],

    selected_album: null

  },
  mutations: {
    toggleDrawer: state => state.show_drawer = !state.show_drawer,
    setActiveCategory: (state, category) => {
      if (category.localeCompare('Show all categories') == 0){
        state.active_categories = state.categories.map(tuple => tuple.text)
        state.active_categories = state.active_categories.filter(category => category.localeCompare('Show all categories') != 0)
      } else {
        state.active_categories = [category]
      }
    },
    setDrawerState: (state, intended_state) => {
      state.show_drawer = intended_state
    },
    setFilteredAlbums: (state, filtered_albums) => {
      state.filtered_albums = filtered_albums
    },
    setAlbumView: (state, selected_album) =>
      state.selected_album = selected_album
  },
  getters: {
    getCategories: state => {
      return state.categories
    },
    getAlbums: state => category => {
      return state.albums.filter(album => category.localeCompare(album.type) == 0)
    },
    getFilteredAlbums: state => {
      if (state.filtered_albums.length > 0) {
        return state.filtered_albums
      }
      return state.no_search_results
    },
    getAllAlbums: state => {
      return state.albums
    },
    getActiveCategories: state => {
      return state.active_categories
    },
    getDrawerPages: state => {
      return state.drawer_pages
    },
    getDrawerState: state => {
      return state.show_drawer
    },
    getSelectedAlbum: state => {
      return state.selected_album
    }
  }
})
